.profile__name{
  grid-area: profile__name;
  font-weight: 500;
  font-size: 42px;
  line-height: 1.14;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 335px;
}

@media screen and (max-width:425px) {
  .profile__name{
    line-height: 1.22;
    font-size: 27px;
    margin:4px 0 0 29px;
  }
}
